<template>
    <div class="app">
        <!--  账号信息  -->
        <div class="accountBox">
            <p class="usdLogo"><img :src="currencyInfo.icon" alt=""></p>
            <p class="type">{{currencyInfo.name}}</p>
            <p class="holder">{{currencyInfo.tag}}</p>
            <div class="holderNum pr"><p>持有数</p></div>
            <p class="balance">{{currencyInfo.wallet.total}}</p>
            <div class="content">
                <div class="left">
                    <p class="top">可用余额</p>
                    <p class="bottom">{{currencyInfo.wallet.free}}</p>
                </div>
                <div class="right">
                    <p class="top">冻结金额</p>
                    <p class="bottom">{{currencyInfo.wallet.lock}}</p>
                </div>
            </div>
            <div class="cashRecord">
                <router-link :to="{path:'/rechargelog', query:{currencyId:currencyInfo.id}}">
                    <img src="@/static/images/my/btn_cwjl.png" alt />
                    <span>财务记录</span>
                </router-link>
            </div>
        </div>

        <!--  充值地址  -->
        <p class="mainTip">充值地址</p>
        <div class="erBox">
            <img :src="currencyInfo.address_qrcode" alt class="mainEr" />
        </div>
        <div class="erCode">
            <span id='erCode'>{{currencyInfo.address}}</span>
            <img class="copy" @click="copy(currencyInfo.address)" src="@/static/images/my/btn_copy_red.png" alt/>
        </div>
        <div class="withdrawal" v-if="currencyInfo.name == 'MCT' || currencyInfo.name == 'mct'"><a href="javascript:void(0) " @click="showAlert" >提现</a></div>
        <div class="withdrawal" v-else><router-link :to="{path:'/withdraw', query:{currencyId:currencyInfo.id}}" >提现</router-link></div>
    </div>
</template>

<script>
    import {currencyInfo} from "@/api/user";
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import Clipboard from 'clipboard'

    export default {
        name: "Recharge",
        data(){
            return {
                currencyInfo:{
                    icon:'',
                    name:'',
                    tag:'',
                    address:'',
                    address_qrcode:'',
                    wallet:{total:0, free:0, lock:0}
                },
                currency_id:undefined
            }
        },
        mounted()
        {

            this.currency_id = this.$route.query.currencyId
            if (this.currency_id == '' || typeof (this.currency_id) == "undefined"){

                Dialog.alert({
                    title: '系统提醒',
                    message: '错误访问'
                }).then(() => {
                   this.$router.push({path:'/'})
                })
                return false;
            }
            this.getInfo()
        },
        methods:
        {
           getInfo(){
               currencyInfo(this.currency_id).then(res => {
                  this.currencyInfo = res
               })
           },
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    Toast.success('复制成功');
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    Toast.fail('当前浏览器不支持');
                    // 释放内存
                    clipboard.destroy()
                })

            },
            showAlert()
            {
                Dialog.alert({
                    message: '暂未开放'
                }).then(() => {
                    // on close
                });
            }
        }
    }
</script>

<style scoped>
    @import "../../../static/css/recharge.css";
</style>



